import React from "react"

import { getUsers, sendDataUsersForSave } from "../api"
import { Type }  from 'react-bootstrap-table2-editor'

import UniversalTable from "./UniversalTable"

const ERole = [
	<span className="_mark mark-admin"></span>, 
	<span className="_mark mark-moderator"></span>, 
	<span className="_mark mark-editor"></span>, 
	""]

class Users extends React.Component{
	constructor(props){
		super(props)
		this.columns = [
			{
				text: 'ID',
				dataField: 'id',
				editable: false
			},
			{
				text: '',
				dataField: 'name'
			},
			{
				text: '',
				dataField: 'login',
				editable: false
			},
			{
				text: '',
				dataField: 'role',
				formatter: (cell, row) => { return ERole[row.role] },
				editor: {
					type: Type.SELECT,
					options: [{
					  value: "0",
					  label: ''
					}, {
					  value: "1",
					  label: ''
					}, {
					  value: '2',
					  label: ''
					}, {
					  value: '3',
					  label: ''
					}]
				 }
			},
			{
				text: '',
				dataField: 'active',
				formatter: (cell, row) => { 
					return row.active == 1? 
						<span className="_mark mark-moderator"></span>:
						<span className="_mark mark-admin"> </span>
				},
				editor: {
					type: Type.SELECT,
					options: [{
					  value: '0',
					  label: ' '
					}, {
					  value: '1',
					  label: ''
					}]
				 }
			}
		]
	}
	

	render(){
		return(
			<div className="ManageUsers">
				{/* <h1 style={{textAlign: "center", fontFamily: "RobotoBold", fontSize: "1.5em"}}> </h1> */}
				
				<UniversalTable
					className="users"
					title=" "
					columns={this.columns}

					getDataFromAPI={getUsers}
					saveDataToAPI={sendDataUsersForSave}

					optPagination={true}
					enableNewRow={false}
				/>

				
			</div>
		)
	}
}


export default Users