import React from "react"
import { NotificationManager } from "react-notifications"
import { Form, FloatingLabel } from "react-bootstrap"
import { registration } from "../api"

import "../styles/Forms.scss"

class Registration extends React.Component{
	constructor(props){
		super(props)
		this.state = {
			validateLogin: null,
			validateName: null,
			validatePassword: null
		}

		this.name = React.createRef()
		this.login = React.createRef()
		this.role = React.createRef()
		this.password = React.createRef()
		this.repeatePassword = React.createRef()

	}

	onBlurInput(e){
		switch(e.target.name){
			case "name": return this.setState({ validateName: !(e.target.value.length >= 3) })
			case "login": return this.setState({ validateLogin: !(e.target.value.length >= 4) })
			case "password": return this.setState({ validatePassword: !(e.target.value.length >= 8) })
			default: return;
		}
	}

	registrationUser(e){
		e.preventDefault()
		
		if(this.repeatePassword.current.value === this.password.current.value){
			const data = {
				name: this.name.current.value,
				login: this.login.current.value,
				role: this.role.current.value,
				password: this.password.current.value,
			}
			registration(data, _data => {
				if(!_data.error){
					localStorage.setItem("token", _data.jwt)
					this.props.history.push("/")
					NotificationManager.success('   !  ,    ', '!', 3000);
				}
				else NotificationManager.error('   ', '', 3000)
			}, (err) => NotificationManager.error('   !', '', 3000))
		}
		else
			NotificationManager.warning('  !', '', 3000);
	}
	render(){
		return(
			<div className="main">
				<Form className="registration-form" style={{display: "flex", flexDirection: "column", width: "400px"}}>

					<h2></h2>
					<FloatingLabel hasValidate controlId="floatingInput" label="" className="mb-12">
						<Form.Control 
							className={this.state.validateName !== null? (this.state.validateName? "is-invalid": "is-valid"): ""} 
							name="name" 
							ref={this.name} 
							type="text" 
							placeholder="  " 
							onBlur={e => this.onBlurInput(e)} 
							autoFocus/>
					</FloatingLabel>
					<FloatingLabel controlId="floatingInput" label="" className="mb-12">
						<Form.Control 
							className={this.state.validateLogin !== null? (this.state.validateLogin? "is-invalid": "is-valid"): ""} 
							ref={this.login} 
							name="login"
							type="text" 
							placeholder="Login" 
							onBlur={e => this.onBlurInput(e)}/>
					</FloatingLabel>
					
					<Form.Select name="" id="" ref={this.role}>
						<option value="3"></option>
						<option value="2"></option>
						<option value="1"></option>
						<option value="0"></option>
					</Form.Select>

					<FloatingLabel controlId="floatingInput" label="" className="mb-12">
						<Form.Control 
							className={this.state.validatePassword !== null? (this.state.validatePassword? "is-invalid": "is-valid"): ""} 
							ref={this.password} 
							name="password"
							type="password" 
							placeholder="Login" 
							onBlur={e => this.onBlurInput(e)}/>
					</FloatingLabel>
					<FloatingLabel controlId="floatingInput" label=" " className="mb-12">
						<Form.Control ref={this.repeatePassword} type="password" placeholder="  " />
					</FloatingLabel>
					<button className="btn btn-primary" onClick={this.registrationUser.bind(this)}></button>
				</Form>
			</div>
		)
	}
}

export default Registration